import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import VueAnalytics from 'vue-analytics';
import cyrillicToTranslit from 'cyrillic-to-translit-js';
import VueMoment from 'vue-moment';
import VueDisqus from 'vue-disqus';

import VueFire from 'vuefire';

import firebase from 'firebase/app';
import 'firebase/firestore';
import 'firebase/auth';


import SocialSharing  from 'vue-social-sharing';
Vue.use(SocialSharing);


import fireConfig from './helpers/firebase.config';
import data from './helpers/quotes';
const load = () => {
  const length = data.quotes.length - 1;
  window.addEventListener('load', () => {
    let interval = setInterval(() => {
      let random = Math.round(Math.random()*length);
      document.getElementById("caption").innerHTML = `
        <sub class='quotes'>&#10077;</sub>
          ${data.quotes[random]}
        <sup class='quotes'>&#10078;</sup>
      `;
    }, 1500);
    setTimeout(() => {
        document.getElementById("cube-loader").style.display = "none";
        document.getElementById("app").style.display = "block";
        clearTimeout(interval);
    }, 2500);
})
}

load();

Vue.config.productionTip = false;

Vue.prototype.$_friendlyUrl = function (value) {
    return cyrillicToTranslit({ preset: "uk" }).transform(value.toLowerCase(), "_")
};
Vue.use(VueFire);
Vue.use(VueMoment);
Vue.use(VueAnalytics, {
  id: 'UA-136388096-1',
  router
});

Vue.use(VueDisqus);

firebase.initializeApp(fireConfig);
      
firebase.auth().onAuthStateChanged(() => {
	let app = '';
  	if (!app) {
    	const app = new Vue({
        	router,
        	store,
        	render: h => h(App),
      	}).$mount("#app");
    }
});

Vue.prototype.$_db = firebase.firestore();
Vue.prototype.$_auth = firebase.auth();